@mixin list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 17px;
  font-weight: 700;
}

@mixin link {
  text-decoration: none;
  color: #000000;
}

@mixin center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin hover-opacity {
  &:hover {
    opacity: 0.6;
  }
}

@mixin active-opacity {
  &:active {
    opacity: 0.3;
  }
}
